@use "variables" as *;
@use "mixins" as *;

/* ==========================================================================
   Notices
   ========================================================================== */

/*
   Default notice
   ========================================================================== */

.notice {
  margin-top: 1.5em;
  padding: .5em 1em;
  text-indent: 0;
  @include font-size(16);
  background-color: lighten($black, 95);
  border: 1px solid lighten($black, 90);
  border-radius: 4px;
}

