@use "variables" as *;

// UTILITY FUNCTIONS
// --------------------------------------------------

// em() - Convert pixels to ems
// Usage: em(16) → 1em, em(32) → 2em
@function em($px, $base: $doc-font-size) {
  @return ($px / $base) * 1em;
}

// MIXINS
// --------------------------------------------------

// Webkit-style focus (used via placeholder selector)
%tab-focus {
  outline: thin dotted #333;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}

// Vertical Rhythm Typography
// https://github.com/sturobson/Sassifaction
//
// Usage:
//   @include font-size(24);           - with default margin and line-height
//   @include font-size(24, no);       - no bottom margin
//   @include font-size(24, 32);       - custom bottom margin
//   @include font-size(24, yes, 1);   - line-height: 1
//   @include font-size(24, yes, 40);  - custom line-height

@mixin font-size($size, $margin: yes, $line-height: $doc-line-height) {
  // generates the font-size in REMs with a PX fallback
  font-size: 0px + $size;
  font-size: 0rem + $size / $doc-font-size;

  // line-height
  @if $line-height != $doc-line-height and $line-height != 1 {
    line-height: ceil($size / $line-height) * ($line-height / $size);
  } @else if $line-height == 1 {
    line-height: 1;
  } @else {
    line-height: ceil($size / $doc-line-height) * ($doc-line-height / $size);
  }

  // margin-bottom
  @if $margin == no {
    margin-bottom: 0;
  } @else if $margin != yes and $margin != no {
    margin-bottom: 0px + $margin;
    margin-bottom: 0rem + ($margin / $doc-font-size);
  } @else {
    margin-bottom: 0px + $doc-line-height;
    margin-bottom: 0rem + ($doc-line-height / $doc-font-size);
  }
}
