// TYPOGRAPHY ================================================
$base-font: 'source-sans-pro', sans-serif;
$heading-font: $base-font;
$code-font: 'source-code-pro', monospace;
$alt-font: 'volkhov', serif;

$doc-font-size: 16;
$doc-line-height: 24;

$border-radius: 3px;

// Layout spacing
$gutter: 1.618em; // golden ratio

// set-up the body font-size / line-height
body {
  margin-top: 0px + $doc-line-height;
  font-size: 0px + $doc-font-size;
}

// COLORS =====================================================
$body-color         : #fff;
$text-color         : #222;
$base-color         : #343434;
$border-color       : #ddd;
$white              : #fff;
$black              : #000;
$link-color         : #222;

// CSS CUSTOM PROPERTIES (for future theming) =================
:root {
  --color-text: #{$text-color};
  --color-bg: #{$body-color};
  --color-link: #{$link-color};
  --color-border: #{$border-color};
  --font-body: #{$base-font};
  --font-code: #{$code-font};
  --font-alt: #{$alt-font};
  --border-radius: #{$border-radius};
}

// Table styling (used in TOC)
$table-stripe-color: darken($body-color, 4);
