@use "variables" as *;
@use "mixins" as *;

/* ==========================================================================
   Top navigation
   ========================================================================== */

.navigation-wrapper {
  text-align: center;
  display: flow-root;
  ul {
    display: inline-block;
    width: 100%;
    vertical-align: top;
    margin: 0 0 50px;
    padding: 4px 20px;
    background-color: $black;
    display: flow-root;
    @media (min-width: 768px) {
      width: auto;
      border-radius: 0 0 10px 10px;
    }
  }
  li {
    display: block;
    float: left;
    list-style: none;
    text-align: center;
    @include font-size(24, no);
    text-transform: uppercase;
    color: $white;
    @media (min-width: 768px) {
      @include font-size(14, no);
    }
    a:hover {
      box-shadow: inset 0 0 1px $white;
    }
  }
  a {
    display: block;
    margin-bottom: 10px;
    padding: 12px 20px;
    color: $white;
    border-radius: 4px;
    @media (min-width: 768px) {
      margin-bottom: 0;
      padding: 6px 10px;
    }
    &:hover {
      background-color: lighten($black, 10);
    }
  }
}

#site-nav {
  display: block;
}
