@use "variables" as *;
@use "mixins" as *;

/* ==========================================================================
   Page/post layout and styling
   ========================================================================== */

/*
   Main content
   ========================================================================== */

#main {
	display: flow-root;
}

.entry,
.hentry {
	display: flow-root;
	border-bottom: 1px solid lighten($black,80);
	border-bottom: 1px solid rgba($black,.10);
}

/* feature image */

.entry-feature-image {
	margin: 20px 0 0;
	width: 100%;
	@media (min-width: 768px) {
		margin-top: -75px; /* move up to be overlapped by site logo */
	}
	@media (min-width: 1000px) {
		margin-top: -145px; /* move up further to be overlapped by site logo */
	}
}

/* page header */

.entry-header {
	width: 100%;
	@media (min-width: 1000px) {
		grid-column: 1 / -1;
	}
}

/* tag listing in page header */

.entry-tags {
	margin: 2em 0 0;
  padding: 0;
	text-transform: uppercase;
	@include font-size(16);
	font-weight: 600;
  a {
    color: $text-color;
    padding: 0 5px;
  }
  li {
    display: inline-block;
    margin-bottom: 0;
    &:before {
      content: "\2022";
    }
    &:first-child {
      &:before {
        content: "";
      }
      a {
        padding-left: 0;
      }
    }
  }
}

/* page title */

span + .entry-title {
	margin-top: 0;
}

.entry-title {
	font-family: $alt-font;
	font-style: italic;
	@include font-size(36,yes,36);
	font-weight: 400;
	line-height: 1;
	letter-spacing: -3px;
	a {
		color: $black;
		text-decoration: underline;
	}
	@media (min-width: 768px) {
		@include font-size(52,yes,54);
	}
	@media (min-width: 1000px) {
		@include font-size(68,yes,72);
	}
}

/* page/post wrapper */

.entry-wrapper {
	max-width: 960px;
	margin: 0 auto;
	margin-bottom: 3em;
	padding-right: $gutter;
	padding-left: $gutter;
	@media (min-width: 1000px) {
		display: grid;
		grid-template-columns: 1fr 3.8fr;
		gap: 0 $gutter;
	}
}

/* page/post meta content (date, author, etc) */

.entry-meta {
	width: 100%;
	text-transform: uppercase;
	@include font-size(14);
	a {
		color: $text-color;
	}
	@media (min-width: 1000px) {
		grid-column: 1;
		grid-row: 2;
	}
	& > span {
		padding: 0 20px 10px 0;
		display: inline-block;
		@media (min-width: 1000px) {
			display: block;
			padding: 8px 0;
			border-bottom: 1px solid lighten($black,80);
			border-bottom: 1px solid rgba($black,.10);
		}
	}
}

/* author avatar (circular) */

.bio-photo {
	display: none;
	@media (min-width: 1000px) {
		display: block;
		width: 150px;
		height: 150px;
		margin-bottom: 10px;
		border-radius: 150px;
		display: flow-root;
	}
}


/* feature image credit */

.image-credit {
  margin: 0 auto;
  max-width: 440px;
  padding-top: 5px;
  padding-right: 20px;
  padding-left: 20px;
  text-align: right;
  @include font-size(12, no);
  line-height: 1.3;
  color: lighten($text-color, 30);
  display: flow-root;
  @media (min-width: 768px) {
    max-width: 760px;
    padding-right: 60px;
    padding-left: 60px;
    @include font-size(14, no);
  }
  @media (min-width: 1000px) {
    max-width: 960px;
  }
  a {
    color: lighten($text-color, 30);
  }
}

/* main content block */

.entry-content {
	width: 100%;
	min-width: 0; /* Allow content to shrink in CSS Grid */
	p:first-child {
		margin-top: 0;
	}
	@media (min-width: 1000px) {
		grid-column: 2;
		grid-row: 2;
	}
	/* nice link underlines */
  p > a,
	li > a {
		border-bottom: 1px dotted lighten($link-color, 50);
		&:hover {
			border-bottom-style: solid;
		}
	}
}

/*
   Disqus
   ========================================================================== */

#disqus_thread {
	margin-top: 2em;
}

/*
   Pagination
   ========================================================================== */

.pagination {
	margin-top: 2em;
	text-align: center;
}

/*
   Overrides
   ========================================================================== */

/* adjust width for lack of meta/author column */

#page {
	.entry-wrapper {
		max-width: em(760);
		display: block; /* disable grid for simple pages */
	}
	.entry-title {
		text-align: center;
		max-width: 100%;
	}
	.entry-content {
		width: 100%;
	}
}

/*
   Kramdown generated table of contents
   ========================================================================== */

#markdown-toc {
	font-family: $alt-font;
	margin-top: $gutter;
	margin-bottom: $gutter;
	padding-left: 0;
	border: 1px solid $border-color;
	border-radius: $border-radius;
  ul {
  	list-style-type: none;
  	padding-left: 0;
  }
  li {
    @include font-size(16,no,18);
    border-bottom: 1px solid $border-color;
    list-style-type: none;
  }
  h6 {
    margin: 0;
    padding: (.25 * $gutter) (.5 * $gutter);
    background: $table-stripe-color;
  }
  a {
    display: block;
    padding: (.25 * $gutter) (.5 * $gutter);
    border-left: 2px solid transparent;
    border-bottom: 0 solid transparent;
    &:hover,
    &:focus {
      background: lighten($border-color,5);
    }
  }
}

