@use "variables" as *;
@use "mixins" as *;

/* ==========================================================================
   Buttons
   ========================================================================== */

/*
   Default button
   ========================================================================== */

.btn {
  display: inline-block;
  margin-bottom: 20px;
  padding: 8px 20px;
  @include font-size(14);
  background-color: $black;
  color: $white;
  border: 2px solid $black !important;
  border-radius: $border-radius;
  &:visited {
    color: $white;
  }
  &:hover {
    background-color: $white;
    color: $black;
  }
}

