@use "variables" as *;
@use "mixins" as *;

/* ==========================================================================
   Masthead
   ========================================================================== */

.masthead {
  margin-top: 40px;
  display: flow-root;
  @media (min-width: 768px) {
    margin-top: 0;
  }
  .wrap {
    text-align: center;
    max-width: em(440);
    padding-right: 20px;
    padding-left: 20px;
    @media (min-width: 768px) {
      max-width: em(728);
      padding-right: 60px;
      padding-left: 60px;
    }
    @media (min-width: 1000px) {
      max-width: em(960);
    }
  }
}

/*
   Site logo
   ========================================================================== */

.site-logo {
  img {
    width: 100px;
    height: 100px;
    animation-duration: 1s;
      animation-delay: .05s;
      border: 4px solid $white;
      border-radius: 100px; /* crops in a circle */
    @media (min-width: 768px) {
      width: 150px;
      height: 150px;
      border-radius: 150px; /* crops in a circle */
    }
    @media (min-width: 1000px) {
      width: 200px;
      height: 200px;
      border-radius: 200px; /* crops in a circle */
    }
  }
}

/*
   Site title/name
   ========================================================================== */

.site-title {
  margin-bottom: 0;
  animation-duration: 1s;
    animation-delay: .75s;
  a {
    color: $black;
  }
}

/*
   Site description text
   ========================================================================== */

.site-description {
  margin-top: 0;
  font-family: $alt-font;
  @include font-size(16);
  font-weight: 400;
  font-style: italic;
  animation-duration: 1s;
    animation-delay: 1s;
  @media (min-width: 768px) {
    @include font-size(20);
  }
}
