@use "variables" as *;
@use "mixins" as *;

/* ==========================================================================
   Site footer
   ========================================================================== */

.footer-wrapper {
  display: flow-root;
  margin: 2em auto;
  text-align: center;
  font-family: $alt-font;
  @include font-size(14);
  font-style: italic;
  color: lighten($text-color,20);
  a {
    color: lighten($text-color,20);
  }
  .entry-wrapper {
    display: block; /* override grid layout for footer */
  }
}

/*
   Social media icons
   ========================================================================== */

.social-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0;
  margin: 1em 0 2em;
  a {
    display: inline-flex;
    align-items: center;
    padding: 4px 8px;
    &:hover {
      .social-icon {
        fill: $black;
      }
    }
  }
  .social-icon {
    width: 24px;
    height: 24px;
    fill: lighten($text-color, 20);
  }
}
